@import url(https://fonts.googleapis.com/css?family=Lato:300italic);
@import url(https://fonts.googleapis.com/css?family=Titan+One);
@import url(https://fonts.googleapis.com/css?family=Lobster);
html,
body {
    margin:0;
    padding: 0;
    /*width: 100%;*/
    /*height: 100%;*/
}

.cake-page {
    background: #d54d56;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

#cake {
    display: block;
    position: relative;
    margin: -10em auto 0 auto;
}

/* ============================================== Candle
*/

.velas {
    background: #ffffff;
    border-radius: 10px;
    position: absolute;
    top: 228px;
    left: 50%;
    margin-left: -2.4px;
    margin-top: -8.33333333px;
    width: 5px;
    height: 35px;
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-animation: in 500ms 6s ease-out forwards;
            animation: in 500ms 6s ease-out forwards;
}
.velas:after,
.velas:before {
    background: rgba(255, 0, 0, 0.4);
    content: "";
    position: absolute;
    width: 100%;
    height: 2.22222222px;
}
.velas:after {
    top: 25%;
    left: 0;
}
.velas:before {
    top: 45%;
    left: 0;
}

/* ============================================== Fire
*/

.fuego {
    border-radius: 100%;
    position: absolute;
    top: -20px;
    left: 50%;
    margin-left: -2.6px;
    width: 6.66666667px;
    height: 18px;
}
.fuego:nth-child(1) {
    -webkit-animation: fuego 2s 6.5s infinite;
            animation: fuego 2s 6.5s infinite;
}
.fuego:nth-child(2) {
    -webkit-animation: fuego 1.5s 6.5s infinite;
            animation: fuego 1.5s 6.5s infinite;
}
.fuego:nth-child(3) {
    -webkit-animation: fuego 1s 6.5s infinite;
            animation: fuego 1s 6.5s infinite;
}
.fuego:nth-child(4) {
    -webkit-animation: fuego 0.5s 6.5s infinite;
            animation: fuego 0.5s 6.5s infinite;
}
.fuego:nth-child(5) {
    -webkit-animation: fuego 0.2s 6.5s infinite;
            animation: fuego 0.2s 6.5s infinite;
}

/* ============================================== Animation Fire
*/

@-webkit-keyframes fuego {
    0%, 100% {
        background: rgba(254, 248, 97, 0.5);
        box-shadow: 0 0 40px 10px rgba(248, 233, 209, 0.2);
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
    50% {
        background: rgba(255, 50, 0, 0.1);
        box-shadow: 0 0 40px 20px rgba(248, 233, 209, 0.2);
        -webkit-transform: translateY(-20px) scale(0);
                transform: translateY(-20px) scale(0);
    }
}

@keyframes fuego {
    0%, 100% {
        background: rgba(254, 248, 97, 0.5);
        box-shadow: 0 0 40px 10px rgba(248, 233, 209, 0.2);
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
    50% {
        background: rgba(255, 50, 0, 0.1);
        box-shadow: 0 0 40px 20px rgba(248, 233, 209, 0.2);
        -webkit-transform: translateY(-20px) scale(0);
                transform: translateY(-20px) scale(0);
    }
}

@-webkit-keyframes in {
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes in {
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.text {
    color: #fff;
    font-family: 'Lobster', cursive;
    font-weight: 300;
    font-style:italic;
    text-align: center;
    letter-spacing: 1px;
    text-transform: capitalize;
    line-height: 0.8;
}
h1 {
    font-size: 2em;}

html {
    font-family: Fort-Medium,Franklin Gothic,sans-serif;
}

.congratulations-page {
    background: #ffffff;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
}

.congratulations-page__balloons {
    width: 100%;
    height: 80vh;
    background-image: url(/boodyvo/happybday.github.io/static/media/balloons.2c3d3d3a.jpg);
    background-position: bottom;
    background-size: contain;
    background-repeat: no-repeat;
}

.congratulations-page__text {
    font-family: 'Lobster', cursive;
    font-weight: 200;
    font-style: italic;
    text-align: center;
    letter-spacing: 1px;
    text-transform: capitalize;
    font-size: calc(1.5rem + 1vw);
    line-height: 1.1em;
}

.columns__video {
    width: 100%;
}

.intro-cta__cup {
    bottom: 1vh;
    left: -5vw;
    right: -1vw;
    height: 30vh;
    /*background-image: url(https://booksmakeusbetter.penguinrandomhouse.com/static/HAND_02-e192fa3bb11c968494afaccdc2a9556a.png);*/
    background-image: url(/boodyvo/happybday.github.io/static/media/cup.2fd6aa39.jpg);
    background-position: bottom;
    background-size: contain;
    background-repeat: no-repeat;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    z-index: 0;
    pointer-events: none;
}

.columns__column-inner {
    position: relative;
    height: 100%;
}

.intro-cta {
    width: 100%;
    height: 100%;
    text-align: center;
}

.intro-cta__button {
    font-family: 'Lobster', cursive;
    position: relative;
    z-index: 1;
    width: auto;
    background-color: Transparent;
    outline: 0;
    padding: 2vh 0 0;
    font-size: calc(1.5rem + 1vw);
    line-height: 1.1em;
    border: none;
    border-bottom: .1em solid #000;
}

.intro-cta__button:hover {
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .columns__column--a--text {
        display: inline-block;
        text-align: left;
        max-width: 100%;
        font-size: calc(.8rem + 1vw);
        line-height: 1.25em;
    }

    .intro-cta__button {
        margin-top: 2vh;
    }
}



